import React, { useState } from 'react'
import { connect } from 'react-redux'
import { register } from '../../redux/auth-reducer'
import { Link, Redirect } from 'react-router-dom'
import './Register.module.css'
import 'antd/dist/antd.min.css'
import { AppStateType } from '../../redux/redux-store'
import { Form, Input, InputNumber, Cascader, Select, Row, Col, Checkbox, Button, Tabs } from 'antd'
import { compose } from 'redux'
import { withRouter } from 'react-router'

const { TabPane } = Tabs

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
}
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
}

type RegisterFormValuesEmailType = {
  email: string
  password: any
  password1: any
}

type RegisterFormValuesPhoneNumberType = {
  phone_number: string
  password: any
  password1: any
}

const RegisterForm: React.FC<MapStatePropsType & MapDispatchPropsEmailType & MapDispatchPropsPhoneType> = (props) => {
  const onFinishEmail = (formData: RegisterFormValuesEmailType) => {
    props.register(formData.email, formData.password, formData.password1)
    props.history.push('/Webshop')
  }

  const onFinishPhone = (formData: RegisterFormValuesPhoneNumberType) => {
    props.register(formData.phone_number, formData.password, formData.password1)
    props.history.push('/Webshop')
  }

  const [formEmail] = Form.useForm()
  const [formPhone] = Form.useForm()

  return (
    <div style={{ marginRight: '20%' }}>
      <Tabs
        defaultActiveKey="1"
        size={'middle'}
        style={{
          marginBottom: 15,
          marginLeft: '20%',
        }}
        centered={true}
      >
        <TabPane tab="Register by email" key="1" style={{ textAlign: 'center' }}>
          <Form
            {...formItemLayout}
            form={formEmail}
            name="register"
            onFinish={onFinishEmail}
            initialValues={{
              prefix: '+7',
            }}
            scrollToFirstError
            style={{
              maxWidth: '700px',
              margin: '0 auto',
              marginRight: '22%',
            }}
          >
            <Form.Item
              name="email"
              label="E-mail"
              rules={[
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your E-mail!',
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              name="password"
              label="Password"
              rules={[
                {
                  required: true,
                  message: 'Please input your password!',
                },
              ]}
              hasFeedback
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              name="password1"
              label="Confirm Password"
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: 'Please confirm your password!',
                },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve()
                    }

                    return Promise.reject(new Error('The two passwords that you entered do not match!'))
                  },
                }),
              ]}
            >
              <Input.Password />
            </Form.Item>
            <Form.Item
              valuePropName="checked"
              rules={[
                {
                  validator: (_, value) =>
                    value ? Promise.resolve() : Promise.reject(new Error('Should accept agreement')),
                },
              ]}
              {...tailFormItemLayout}
            >
              <Checkbox>
                I have read the <a href="">agreement</a>
              </Checkbox>
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
              <Button
                type="primary"
                htmlType="submit"
                style={{ width: '100%' }}
                onSubmit={() => <Redirect to={'/Webshop'} />}
              >
                Register
              </Button>
            </Form.Item>
          </Form>
        </TabPane>

        <TabPane tab="Register by phone" key="2" style={{ textAlign: 'center' }}>
          <Form
            {...formItemLayout}
            form={formPhone}
            name="register"
            onFinish={onFinishPhone}
            initialValues={{
              prefix: '+7',
            }}
            scrollToFirstError
            style={{
              maxWidth: '700px',
              margin: '0 auto',
              marginRight: '22%',
            }}
          >
            <Form.Item
              name="phone_number"
              label="Phone Number"
              rules={[
                {
                  required: true,
                  message: 'Please input your phone number!',
                },
              ]}
            >
              <Input
                style={{
                  width: '100%',
                }}
              />
            </Form.Item>

            <Form.Item
              name="password"
              label="Password"
              rules={[
                {
                  required: true,
                  message: 'Please input your password!',
                },
              ]}
              hasFeedback
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              name="password1"
              label="Confirm Password"
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: 'Please confirm your password!',
                },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve()
                    }

                    return Promise.reject(new Error('The two passwords that you entered do not match!'))
                  },
                }),
              ]}
            >
              <Input.Password />
            </Form.Item>
            <Form.Item
              valuePropName="checked"
              rules={[
                {
                  validator: (_, value) =>
                    value ? Promise.resolve() : Promise.reject(new Error('Should accept agreement')),
                },
              ]}
              {...tailFormItemLayout}
            >
              <Checkbox>
                I have read the <a href="">agreement</a>
              </Checkbox>
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
              <Button
                type="primary"
                htmlType="submit"
                style={{ width: '100%' }}
                onSubmit={() => <Redirect to={'/Webshop'} />}
              >
                Register
              </Button>
            </Form.Item>
          </Form>
        </TabPane>
      </Tabs>
    </div>
  )
}

type MapStatePropsType = {
  history: any
}
type MapDispatchPropsEmailType = {
  register: (email: string, password: any, password1: any) => void
}

type MapDispatchPropsPhoneType = {
  register: (phone_number: string, password: any, password1: any) => void
}

const mapStateToProps = (state: AppStateType) => ({})

export default compose<React.ComponentType>(connect(mapStateToProps, { register }), withRouter)(RegisterForm)
