const ADD_MESSAGE = 'ADD-MESSAGE'

type DialogType = {
  id: number
  name: string
  photo: string
}
type MessageType = {
  id: number
  message: string
}

const initialState = {
  dialogs: [
    { id: 1, name: 'Mikhail', photo: 'https://bit.ly/3kpaHvT' },
    { id: 2, name: 'Andrey', photo: 'https://bit.ly/3kpaHvT' },
    { id: 3, name: 'Sasha', photo: 'https://bit.ly/3kpaHvT' },
    { id: 4, name: 'Victor', photo: 'https://bit.ly/3kpaHvT' },
  ] as Array<DialogType>,
  messages: [
    { id: 1, message: 'Hi' },
    { id: 2, message: 'How are you' },
    { id: 3, message: 'Yo' },
    { id: 4, message: 'Hello' },
  ] as Array<MessageType>,
}

export type InitialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: any): InitialStateType => {
  switch (action.type) {
    case ADD_MESSAGE:
      const body = action.newPostText
      return {
        ...state,
        messages: [...state.messages, { id: 6, message: body }],
      }
    default:
      return state
  }
}

type SendMessageCreatorActionType = {
  type: typeof ADD_MESSAGE
  newMessageBody: string
}

export const sendMessageCreator = (newMessageBody: string): SendMessageCreatorActionType => ({
  type: ADD_MESSAGE,
  newMessageBody,
})
export default dialogsReducer
