import React, { useState } from 'react'
import 'antd/dist/antd.min.css'
import { Card, Carousel, List, Pagination } from 'antd'
import './HotProducts.module.css'
import { AppStateType } from '../../../redux/redux-store'
import { compose } from 'redux'
import { connect } from 'react-redux'
import { getHotProducts } from '../../../redux/product-reducer'
import { withRouter } from 'react-router-dom'

// TODO: нужно зарефакторить отправленные запросы, нужно сделать так, чтобы было оптимизированно

class HotProducts extends React.Component<{}, { current: number }> {
  constructor(props: any) {
    super(props)
    this.state = {
      current: 1,
    }
  }

  onChange = (page: any) => {
    this.setState({ current: page })
    this.refreshProfile(page)
  }

  refreshProfile(page: null | number) {
    // @ts-ignore
    this.props.getHotProducts(page)
  }

  componentDidMount() {
    this.refreshProfile(null)
  }

  componentDidUpdate(prevProps: any) {
    // @ts-ignore
    if (JSON.stringify(this.props.hotProducts) !== JSON.stringify(prevProps.hotProducts)) {
      this.onChange(this.state.current)
    }
  }

  componentWillUnmount(): void {}

  render() {
    // @ts-ignore
    if (this.props.hotProducts != null) {
      // @ts-ignore
      const data = this.props.hotProducts.results
      // @ts-ignore
      const total = this.props.hotProducts.count
      return (
        <div>
          <List
            grid={{
              gutter: 5,
              column: 5,
            }}
            dataSource={data}
            renderItem={(item: any) => (
              <List.Item>
                <Card title={item.name}>
                  <img
                    src={process.env.REACT_APP_PRODUCTS_HOST + item.media}
                    style={{ height: '75%', width: '100%' }}
                  />
                  <br /> <br />
                  <p>Price: {item.full_price}</p>
                  <br />
                </Card>
              </List.Item>
            )}
          />
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <Pagination current={this.state.current} onChange={this.onChange} total={total} pageSize={5} />
          </div>
        </div>
      )
    }
  }
}

const mapStateToProps = (state: AppStateType) => ({
  hotProducts: state.products.hotProducts,
})

export default compose<React.ComponentType>(connect(mapStateToProps, { getHotProducts }), withRouter)(HotProducts)
