import React, { useEffect, useState } from 'react'
import { Tabs } from 'antd'

const { TabPane } = Tabs

export const TreeMenu: React.FC<any> = ({ level = 1, menu: menuProp, ...props }) => {
  const [menu, setMenu] = useState(menuProp || [])
  const [paddingLeft, setPaddingLeft] = useState<number>()

  useEffect(() => {
    if (level) {
      setPaddingLeft(1 + level)
    }
  }, [level])

  return (
    <div>
      <Tabs
        defaultActiveKey="1"
        tabPosition={'left'}
        style={{
          height: '100%',
          marginLeft: '12%',
        }}
      >
        {menu.map((item: any) =>
          item.children === undefined ? (
            <TabPane tab={item.data.name} key={item.id} disabled={false}></TabPane>
          ) : (
            <TabPane tab={item.data.name} key={item.id} disabled={false}>
              {item.children && <TreeMenu menu={item.children} level={level + 1} />}
            </TabPane>
          ),
        )}
      </Tabs>
    </div>
  )
}
