import React from 'react'
import { connect } from 'react-redux'
import { login } from '../../redux/auth-reducer'
import { Link, Redirect } from 'react-router-dom'
import './Login.module.css'
import 'antd/dist/antd.min.css'
import { AppStateType } from '../../redux/redux-store'
import { Form, Input, Button, Checkbox } from 'antd'
import { UserOutlined, LockOutlined } from '@ant-design/icons'

export type LoginFormValuesType = {
  email: string
  password: string
}

const LoginForm: React.FC<MapStatePropsType & MapDispatchPropsType> = (props) => {
  function handleSubmit(formData: LoginFormValuesType) {
    props.login(formData.email, formData.password)
  }

  if (props.isAuth) {
    return <Redirect to={'/Webshop/profile'} />
  }

  return (
    <div style={{ marginRight: '4%' }}>
      <h1 style={{ textAlign: 'center' }}>Login</h1>
      <Form
        name="login"
        className="login-form"
        initialValues={{
          remember: true,
        }}
        style={{
          maxWidth: '300px',
          margin: '0 auto',
        }}
        onFinish={handleSubmit}
      >
        <Form.Item
          name="email"
          rules={[
            {
              required: true,
              message: 'Please input your Email!',
            },
          ]}
        >
          <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Login" name="email" />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[
            {
              required: true,
              message: 'Please input your Password!',
            },
          ]}
        >
          <Input
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="password"
            placeholder="Password"
            name="password"
          />
        </Form.Item>
        <Form.Item>
          <Form.Item valuePropName="checked" noStyle>
            <Checkbox style={{ float: 'left' }}>Remember me</Checkbox>
          </Form.Item>

          <a className="login-form-forgot" href="" style={{ float: 'right' }}>
            Forgot password
          </a>
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit" className="login-form-button" style={{ width: '100%' }}>
            Log in
          </Button>
          Or <Link to="/Webshop/register">register now!</Link>
        </Form.Item>
      </Form>
    </div>
  )
}

type MapStatePropsType = {
  isAuth: boolean
}
type MapDispatchPropsType = {
  login: (email: string, password: string) => void
}

const mapStateToProps = (state: AppStateType) => ({
  isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, { login })(LoginForm)
