import { productsAPI } from '../api/api'
import { stopSubmit } from 'redux-form'
import { CategoryType, HotProductsType } from '../types/types'
import dialogs from '../components/Dialogs/Dialogs'

const SET_CATEGORIES = 'SET_CATEGORIES'
const SET_HOT_PRODUCTS = 'SET_HOT_PRODUCTS'

const initialState = {
  category: null as CategoryType | null,
  hotProducts: null as HotProductsType | null,
}

export type InitialStateType = typeof initialState

// @ts-ignore
const productReducer = (state = initialState, action: any): InitialStateType => {
  switch (action.type) {
    case SET_CATEGORIES: {
      return { ...state, category: action.category }
    }
    case SET_HOT_PRODUCTS: {
      return { ...state, hotProducts: action.hotProducts }
    }
    default:
      return state
  }
}

type SetCategoryActionType = {
  type: typeof SET_CATEGORIES
  category: CategoryType
}
type SetHotProductsActionType = {
  type: typeof SET_HOT_PRODUCTS
  hotProducts: HotProductsType
}
export const setCategory = (category: CategoryType): SetCategoryActionType => ({ type: SET_CATEGORIES, category })
export const setHotProducts = (hotProducts: HotProductsType): SetHotProductsActionType => ({
  type: SET_HOT_PRODUCTS,
  hotProducts,
})

export const getCategories = () => async (dispatch: any) => {
  const response = await productsAPI.getCategories()
  dispatch(setCategory(response.data))
}

export const getHotProducts = (page: number, page_size: number) => async (dispatch: any) => {
  const response = await productsAPI.getHotProducts(page, page_size)
  dispatch(setHotProducts(response.data))
}

export default productReducer
