import React from 'react'
// @ts-ignore
import s from './../Dialogs.module.css'

type PropsType = {
  id: number
  message: string
}

const Message: React.FC<PropsType> = (props) => {
  if (props.id % 2 === 0) {
    return <div className={s.message}>{props.message}</div>
  } else {
    return <div className={s.message}>{props.message}</div>
  }
}

export default Message
