import { authAPI, ResultCodeForCapctha, ResultCodesEnum, securityAPI } from '../api/api'
import { stopSubmit } from 'redux-form'
import React from 'react'

const SET_USER_DATA = 'SET_USER_DATA'
const GET_CAPTCHA_URL_SUCCESS = 'GET_CAPTCHA_URL_SUCCESS'

const initialState = {
  userId: null as number | null,
  email: null as string | null,
  login: null as string | null,
  isAuth: false as boolean,
}

export type InitialStateType = typeof initialState

const authReducer = (state = initialState, action: any): InitialStateType => {
  switch (action.type) {
    case SET_USER_DATA:
    case GET_CAPTCHA_URL_SUCCESS:
      return {
        ...state,
        ...action.payload,
      }

    default:
      return state
  }
}

export const actions = {
  setAuthUserData: (userId: number | null, email: string | null, login: string | null, isAuth: boolean) =>
    ({
      type: 'SN/auth/SET_USER_DATA',
      payload: { userId, email, login, isAuth },
    } as const),
  getCaptchaUrlSuccess: (captchaUrl: string) =>
    ({
      type: 'SN/auth/GET_CAPTCHA_URL_SUCCESS',
      payload: { captchaUrl },
    } as const),
}

type SetAuthUserDataActionPayloadType = {
  userId: number | null
  email: string | null
  login: string | null
  isAuth: boolean
}

type SetAuthUserDataActionType = {
  type: typeof SET_USER_DATA
  payload: SetAuthUserDataActionPayloadType
}

export const setAuthUserData = (
  userId: number | null,
  email: string | null,
  login: string | null,
  isAuth: boolean,
): SetAuthUserDataActionType => ({
  type: SET_USER_DATA,
  payload: { userId, email, login, isAuth },
})

export const getAuthUserData = (id: number, email: string, login: string) => async (dispatch: any) => {
  dispatch(setAuthUserData(id, email, login, true))
}

export const login = (email: string, password: string) => async (dispatch: any) => {
  const data = await authAPI.login(email, password)
  if (data !== null && data.non_field_errors === undefined) {
    dispatch(getAuthUserData(data.user.id, data.user.email, data.user.full_name))
  }
  const message = data.non_field_errors != null ? data.non_field_errors[0] : 'Some error'
  dispatch(stopSubmit('login', { _error: message }))
}

export const logout = () => async (dispatch: any) => {
  await authAPI.logout()
  dispatch(setAuthUserData(null, null, null, false))
}

export const register = (email_phone: string, password: any, password1: any) => async (dispatch: any) => {
  if (email_phone.match(/^[+]\d+$/) === null) {
    const email = email_phone
    await authAPI.registerEmail(email, password, password1)
  } else {
    const phone_number = email_phone
    await authAPI.registerByPhone(phone_number, password, password1)
  }
}

export default authReducer
