import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { AppStateType } from '../../../redux/redux-store'
import { getCategories } from '../../../redux/product-reducer'
import { Button, Col, Menu, Popover, Row, Tabs } from 'antd'
import { TreeMenu } from './TreeMenu'

const { TabPane } = Tabs

// TODO: TO FIND WHERE FIRST NULL IS COMING

class Category extends React.Component {
  refreshProfile() {
    // @ts-ignore
    this.props.getCategories()
  }

  componentDidMount() {
    this.refreshProfile()
  }

  componentDidUpdate(prevProps: any) {
    // @ts-ignore
    if (JSON.stringify(this.props.category) !== JSON.stringify(prevProps.category)) {
      this.refreshProfile()
    }
  }

  componentWillUnmount(): void {}

  // @ts-ignore
  render() {
    // @ts-ignore
    if (this.props.category !== null) {
      // @ts-ignore
      const category = this.props.category
      return (
        <div>
          <TreeMenu menu={category} />
        </div>
      )
    }
  }
}

const mapStateToProps = (state: AppStateType) => ({
  category: state.products.category,
})

export default compose<React.ComponentType>(connect(mapStateToProps, { getCategories }), withRouter)(Category)
