import axios from 'axios'
import { ProfileType } from '../types/types'

/** API method prefixes:
 *  get... - method GET
 *  create... - method POST
 *  set... - method PUT
 *  delete... - method DELETE
 */

const instance = axios.create({
  baseURL: process.env.REACT_APP_ENDPOINT,
})

export enum ResultCodesEnum {
  Success = 0,
  Error = 1,
}

export enum ResultCodeForCapctha {
  CaptchaIsRequired = 10,
}

type LoginResponseType = {
  access_token: string
  refresh_token: string
  access_token_expiration: string
  refresh_token_expiration: string
  user: {
    id: number
    email: string
    full_name: string
  }
  non_field_errors: null | Array<number>
}

type RegisterEmailResponseType = {
  email: string
}

type RegisterPhoneResponseType = {
  phone_number: string
}

export const usersAPI = {
  getUsers(currentPage = 1, pageSize = 10) {
    return instance.get(`users?page=${currentPage}&count=${pageSize}`).then((response) => {
      return response.data
    })
  },
  follow(userId: number) {
    return instance.post(`follow/${userId}`)
  },
  unfollow(userId: number) {
    return instance.delete(`follow/${userId}`)
  },
  getProfile(userId: number) {
    return instance.get(`profile/` + userId)
  },
}

export const authAPI = {
  login(email: string, password: string) {
    return instance.post<LoginResponseType>(`/api/v1/sign-in/`, { email, password }).then((res) => res.data)
  },
  logout() {
    return instance.post(`/api/v1/logout/`)
  },
  registerEmail(email: string, password: string, password1: string) {
    return instance.post<RegisterEmailResponseType>(`/api/v1/sign-up/email/`, {
      email,
      password,
      password1,
    })
  },
  registerByPhone(phone_number: string, password: string, password1: string) {
    return instance.post<RegisterPhoneResponseType>(`/api/v1/sign-up/phone/`, {
      phone_number,
      password,
      password1,
    })
  },
}

export const productsAPI = {
  getCategories() {
    return instance.get('/api/v1/categories/')
  },
  getHotProducts(page = 1, pageSize = 5) {
    return instance.get(`/api/v1/hot-products/?page=${page}&page_size=${pageSize}`)
  },
}

export const profileAPI = {
  getProfile(userId: number) {
    return instance.get(`profile/` + userId)
  },
  getStatus(userId: number) {
    return instance.get(`profile/status/` + userId)
  },
  updateStatus(status: string) {
    return instance.put(`profile/status`, { status: status })
  },
  savePhoto(photoFile: any) {
    const formData = new FormData()
    formData.append('image', photoFile)

    return instance.put(`profile/photo`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    })
  },
  saveProfile(profile: ProfileType) {
    return instance.put(`profile`, profile)
  },
}

export const securityAPI = {
  getCaptchaUrl() {
    return instance.get(`security/get-captcha-url`)
  },
}
