import React from 'react'
// @ts-ignore
import s from './Dialogs.module.css'
import DialogItem from './DialogItem/DialogItem'
import Message from './Message/Message'
import AddMessageForm from './AddMessageForm/AddMessageForm'
import { InitialStateType } from '../../redux/dialogs-reducer'

type PropsType = {
  dialogsPage: InitialStateType
  sendMessage: (messageText: string) => void
}

export type NewMessageFormValuesType = {
  newPostText: string
}

const Dialogs: React.FC<PropsType> = (props) => {
  const state = props.dialogsPage
  const dialogsElements = state.dialogs.map((dialog) => (
    <DialogItem name={dialog.name} key={dialog.id} id={dialog.id} photo={dialog.photo} />
  ))
  const messagesElements = state.messages.map((message) => (
    <Message message={message.message} key={message.id} id={message.id} />
  ))
  const addNewMessage = (values: NewMessageFormValuesType) => {
    props.sendMessage(values.newPostText)
  }

  return (
    <div className={s.dialogs}>
      <div className={s.dialogsItems}>{dialogsElements}</div>
      <div className={s.messages}>{messagesElements}</div>
      <AddMessageForm onSubmit={addNewMessage} />
    </div>
  )
}

export default Dialogs
